.container-hero{
    height: 80vh;
    background: linear-gradient(90deg, #9022c3e2, rgb(147, 39, 255));
    border-radius: 10px;
    margin: 50px 10%;
    color: rgb(255, 255, 255);
    text-align: center;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.hero{
    padding: 50px;
    display: flex;
    justify-content: center;
}
.text-hero{
    padding-top: 10px;
}

.app-container {
    background-image: url('../../public/por.png'); /* Adjust the path based on your project structure */
    background-size: cover; /* or 'contain', depending on how you want the image to be sized */
    background-repeat: no-repeat;
    width: 300px; /* Set the width and height based on your requirements */
    height: 250px;
    border-radius: 50%;
    
  }
.click-on-me{
  position: fixed;
  left: 30%;
  top: 67%; /* Prilagodite po potrebi */
  transform: translateY(-25%); /* Centriranje vertikalno */
  padding: 15px;
  opacity: 0.5;
  color: black;
}


  .image1{
    width: 300px;
        height: 150px;
        border-radius: 20px;
        margin: 40px;
        margin-top: 100px;
  }
  .hinge {
    overflow: hidden; 
    animation: hinge 5s;
    transform-origin: top left;
  }

  .centasr {
    animation: backOutUp 10s ease-out;
    animation-fill-mode: forwards; /* Da održi stil nakon završetka animacije */
  }
  .hidden {
    display: none !important;
  }
  .project{
    display:inline-block;
   
  }
  .pojetct_del{
    animation: fadeOut 1s ease-out; 
    animation-fill-mode: forwards;
  }
  @keyframes fadeOut {
    from {
      opacity: 1;
    }
    to {
      display: none !important;
    }
  }

  .fullphoto1{
    width: 650px;
    height: 350px;
    border-radius: 20px;
  }
  .downimage{
    margin-top: 10px;
  }
  .arrow{
    border-radius: 50%;
    border-style: solid;
    border-width: 0.1rem;
    width: 25px;
    height: 25px;
  }
  @keyframes animate__backOutUp {
    0% {
      opacity: 1;
    }
  

  
    100% {
      display: none !important;
    }
  }
